body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.fa {
  padding: 20px;
  font-size: 30px;
  width: 50px;
  text-align: center;
  text-decoration: none;
  margin: 5px 2px;
}
.fa:hover {
  opacity: 0.7;
}
.fa-github{
  color: black;
}
.fa-twitter {
  color: #00acee;
}
.fa-linkedin{
  color: #0072b1;
}
.fa-instagram{
  color: #8a3ab9;
}
.fa-snapchat-ghost{
  color: rgb(196, 154, 255);
}
@media only screen and (max-width:575px){
  .myname{
    font-size: 5.5vw;
  }
  .myoccupation{
    font-size: 4.5vw;
  }
  #navigation{
    font-size: 3.75vw;
  }
  .myskills{
    font-size: 5.5vw;
  }
  .skills{
    font-size: 4.5vw;
  }
  th{
    font-size: 3.5vw;
  }
  td{
    font-size: 3vw;
  }
  #footer{
    font-size: 2.5vw;
  }
}
.nav-container{
  background-color: #f5f5f5;
}
a{
  cursor: pointer;
}
.table a{
  color: white;
  text-decoration: none;
}